/*Header */
.subMenuHeader ul li {
    border-left: #020202 1px solid;
    border-right: #2f2f2f 1px solid;
}

/*Menu*/
.main-navigation > ul > li > a,
.main-navigation > ul > li > a > i {
    color: #fff;
    font-weight: 700;
}
.main-navigation > ul > li > a:hover,
.main-navigation > ul > li > a:hover > i {
    color: #eee !important;
}

