// }

.bt-header .box-links a:hover,
.bt-header .box-links a.veiculos {
    background-color: #075597;
}

/*Pre�o do Ve�culo da Pagina Home*/
.results-grid-view .result-item-cont {
    background: #040404 !important;
}

.result-item-pricing-home .price {
    color: #fff !important;
}

.chamadaEstoque {
    background: rgba(41, 41, 41, 1);
    background: -moz-radial-gradient(center, ellipse cover, rgba(41, 41, 41, 1) 0%, rgba(19, 19, 19, 1) 100%);
    background: -webkit-gradient(
        radial,
        center center,
        0px,
        center center,
        100%,
        color-stop(0%, rgba(41, 41, 41, 1)),
        color-stop(100%, rgba(19, 19, 19, 1))
    );
    background: -webkit-radial-gradient(center, ellipse cover, rgba(41, 41, 41, 1) 0%, rgba(19, 19, 19, 1) 100%);
    background: -o-radial-gradient(center, ellipse cover, rgba(41, 41, 41, 1) 0%, rgba(19, 19, 19, 1) 100%);
    background: -ms-radial-gradient(center, ellipse cover, rgba(41, 41, 41, 1) 0%, rgba(19, 19, 19, 1) 100%);
    background: radial-gradient(ellipse at center, rgba(41, 41, 41, 1) 0%, rgba(19, 19, 19, 1) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#292929', endColorstr='#131313', GradientType=1 );
}

.chamadaEstoque h2,
.chamadaEstoque p {
    color: #fff !important;
}

/*Pre�o do Ve�culo na Pagina de Detalhes */
.veiculo-detalhes .precoVeiculo {
    background-color: #040404;
    color: #fff !important;
}

/*Bot�es das Paginas*/
.label-primary,
.btn-primary,
.btnFinanciamento {
    background-color: #040404 !important;
    color: #fff !important;
}

.label-primary:hover,
.btn-primary:hover,
.btnFinanciamento:hover {
    background-color: #063f6f !important;
}

.result-item-view-buttons .btn-primary i {
    color: #fff !important;
}

.result-item-view-buttons a i {
    color: #222;
}

.bt-header {
    background: rgba(115, 126, 132, 1);
    background: -moz-linear-gradient(top, rgba(115, 126, 132, 1) 0%, rgba(77, 85, 90, 1) 100%);
    background: -webkit-gradient(
        left top,
        left bottom,
        color-stop(0%, rgba(115, 126, 132, 1)),
        color-stop(100%, rgba(77, 85, 90, 1))
    );
    background: -webkit-linear-gradient(top, rgba(115, 126, 132, 1) 0%, rgba(77, 85, 90, 1) 100%);
    background: -o-linear-gradient(top, rgba(115, 126, 132, 1) 0%, rgba(77, 85, 90, 1) 100%);
    background: -ms-linear-gradient(top, rgba(115, 126, 132, 1) 0%, rgba(77, 85, 90, 1) 100%);
    background: linear-gradient(to bottom, rgba(115, 126, 132, 1) 0%, rgba(77, 85, 90, 1) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#737e84', endColorstr='#4d555a', GradientType=0 );
}

/*Detalhes das Paginas*/
.result-item-pricing .price,
.accent-color,
.accordion-heading .accordion-toggle,
.add-features-list i,
.listing-header strong {
    color: #060606;
}

.hr-style {
    border-top: 3px solid #000000;
}

.separator span {
    background-color: #fe0202;
}
.separator span:after,
.separator span:before {
    border-bottom: 1px solid #fe0202;
}

